
'use client';

import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectGroup, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
// Tabs removed as they are currently unused
import {
    ScrollText,
    Info,
    CheckCircle2,
    Circle,
    TrendingUp,
    ShieldCheck,
    Zap,
    Scale
} from 'lucide-react';
import {
    Course,
    DegreeDomain,
    Grade,
    Level,
    GRADE_POINTS,
    calculateCGPA,
    CURRICULUM,
    predictCoursePerformance,
    PredictionResult
} from '@/lib/grading';
import { motion, AnimatePresence } from 'framer-motion';
import { toast } from 'sonner';

type UserGrades = Record<DegreeDomain, Record<string, Grade>>;
type ManuelOverrides = Record<DegreeDomain, Record<string, Grade>>;

export default function Calculator() {
    const [domain, setDomain] = useState<DegreeDomain>('Data Science');
    const [level, setLevel] = useState<Level>('Foundation');
    const [userGrades, setUserGrades] = useState<UserGrades>({
        'Data Science': {},
        'Electronic Systems': {}
    });
    const [overrides, setOverrides] = useState<ManuelOverrides>({
        'Data Science': {},
        'Electronic Systems': {}
    });
    const [confidence, setConfidence] = useState(1.0);
    const [showPredictions, setShowPredictions] = useState(true);
    const [isInitialized, setIsInitialized] = useState(false);

    // Load from localStorage on mount
    useEffect(() => {
        const saved = localStorage.getItem('iitm-cgpa-v3');
        if (saved) {
            try {
                const parsed = JSON.parse(saved);
                if (parsed.userGrades) setUserGrades(parsed.userGrades);
                if (parsed.overrides) setOverrides(parsed.overrides);
                if (parsed.domain) setDomain(parsed.domain);
                if (parsed.level) setLevel(parsed.level);
                if (parsed.confidence) setConfidence(parsed.confidence);
                if (parsed.showPredictions !== undefined) setShowPredictions(parsed.showPredictions);
            } catch {
                console.error('Failed to load storage');
            }
        }
        setIsInitialized(true);
    }, []); // eslint-disable-next-line react-hooks/exhaustive-deps

    // Save to localStorage
    useEffect(() => {
        if (isInitialized) {
            localStorage.setItem('iitm-cgpa-v3', JSON.stringify({ userGrades, overrides, domain, level, confidence, showPredictions }));
        }
    }, [userGrades, overrides, domain, level, confidence, showPredictions, isInitialized]);

    const currentViewCourses = useMemo(() => {
        const officialList = CURRICULUM[domain][level];

        return officialList.map(c => ({
            id: `${domain}-${level}-${c.name}`,
            name: c.name,
            credits: c.credits,
            level: level,
            domain: domain,
            cluster: c.cluster,
            grade: userGrades[domain][c.name] ?? null
        }));
    }, [domain, level, userGrades]);

    // PREDICTION LOGIC
    const predictions = useMemo(() => {
        if (!showPredictions) return {};

        // All completed courses for context
        const completed: Course[] = [];
        (['Foundation', 'Diploma', 'BSc Degree', 'BS Degree'] as Level[]).forEach(l => {
            CURRICULUM[domain][l].forEach(c => {
                const g = userGrades[domain][c.name];
                if (g && ['S', 'A', 'B', 'C', 'D', 'E'].includes(g)) {
                    completed.push({
                        id: `ctx-${c.name}`,
                        name: c.name,
                        credits: c.credits,
                        level: l,
                        domain: domain,
                        cluster: c.cluster,
                        grade: g
                    });
                }
            });
        });

        const results: Record<string, PredictionResult> = {};
        (['Foundation', 'Diploma', 'BSc Degree', 'BS Degree'] as Level[]).forEach(l => {
            CURRICULUM[domain][l].forEach(c => {
                if (!userGrades[domain][c.name]) {
                    results[c.name] = predictCoursePerformance(
                        { ...c, level: l, domain: domain },
                        completed,
                        confidence
                    );
                }
            });
        });
        return results;
    }, [domain, userGrades, confidence, showPredictions]);

    // Global Statistics
    const globalStats = useMemo(() => {
        const completed: Course[] = [];
        const predictedCourses: Course[] = [];

        (['Foundation', 'Diploma', 'BSc Degree', 'BS Degree'] as Level[]).forEach(l => {
            CURRICULUM[domain][l].forEach(c => {
                const g = userGrades[domain][c.name];
                if (g) {
                    completed.push({
                        id: `c-${c.name}`,
                        name: c.name,
                        credits: c.credits,
                        level: l,
                        domain: domain,
                        cluster: c.cluster,
                        grade: g
                    });
                } else if (showPredictions) {
                    const override = overrides[domain][c.name];
                    const pred = predictions[c.name];
                    if (override || pred) {
                        predictedCourses.push({
                            id: `p-${c.name}`,
                            name: c.name,
                            credits: c.credits,
                            level: l,
                            domain: domain,
                            cluster: c.cluster,
                            grade: override ?? pred.grade
                        });
                    }
                }
            });
        });

        const current = calculateCGPA(completed);
        const projected = calculateCGPA([...completed, ...predictedCourses]);

        return { current, projected };
    }, [domain, userGrades, showPredictions, predictions, overrides]);

    const updateGrade = (courseName: string, grade: Grade) => {
        setUserGrades(prev => ({
            ...prev,
            [domain]: { ...prev[domain], [courseName]: grade }
        }));
    };

    const updateOverride = (courseName: string, grade: Grade) => {
        setOverrides(prev => ({
            ...prev,
            [domain]: { ...prev[domain], [courseName]: grade }
        }));
    };

    if (!isInitialized) return null;

    return (
        <div className="space-y-8 max-w-6xl mx-auto p-4 md:p-8 animate-in fade-in zoom-in-95 duration-700">
            {/* Control Panel */}
            <div className="grid grid-cols-1 lg:grid-cols-12 gap-6">
                <div className="lg:col-span-8 space-y-6">
                    <div className="flex items-center gap-4">
                        <div className="p-4 bg-emerald-600 text-white rounded-3xl shadow-xl shadow-emerald-200 ring-8 ring-emerald-50">
                            <TrendingUp className="w-8 h-8" />
                        </div>
                        <div>
                            <h1 className="text-3xl font-black text-slate-900 tracking-tight">CGPA Predictive Analytics</h1>
                            <p className="text-slate-500 font-bold uppercase text-[10px] tracking-[0.2em]">IIT Madras BS â€¢ ML-Enhanced Planner</p>
                        </div>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 bg-white p-6 rounded-[2rem] border border-slate-100 shadow-sm">
                        <div className="space-y-3">
                            <Label className="text-xs font-black text-slate-400 uppercase tracking-widest px-1">Active Program</Label>
                            <Select value={domain} onValueChange={(v: DegreeDomain) => setDomain(v)}>
                                <SelectTrigger className="h-12 w-full rounded-2xl border-slate-200 bg-slate-50/50">
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent className="rounded-2xl shadow-2xl">
                                    <SelectItem value="Data Science">Data Science</SelectItem>
                                    <SelectItem value="Electronic Systems">Electronic Systems</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-3">
                            <Label className="text-xs font-black text-slate-400 uppercase tracking-widest px-1">Curriculum Stage</Label>
                            <Select value={level} onValueChange={(v: Level) => setLevel(v)}>
                                <SelectTrigger className="h-12 w-full rounded-2xl border-slate-200 bg-slate-50/50">
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent className="rounded-2xl shadow-2xl">
                                    <SelectItem value="Foundation">Foundation</SelectItem>
                                    <SelectItem value="Diploma">Diploma</SelectItem>
                                    <SelectItem value="BSc Degree">BSc Degree</SelectItem>
                                    <SelectItem value="BS Degree">BS Degree</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                    </div>
                </div>

                {/* Dashboard */}
                <Card className="lg:col-span-4 border-none bg-slate-900 text-white rounded-[2.5rem] shadow-2xl overflow-hidden relative">
                    <div className="absolute top-0 right-0 w-48 h-48 bg-emerald-500/20 rounded-full blur-[80px] -mr-24 -mt-24"></div>
                    <CardHeader className="pb-0">
                        <CardTitle className="text-[10px] font-black text-emerald-400 uppercase tracking-[0.3em]">Live Projection</CardTitle>
                    </CardHeader>
                    <CardContent className="pt-4 space-y-6">
                        <div className="space-y-1">
                            <div className="flex items-baseline gap-2">
                                <span className="text-6xl font-black tracking-tighter">{globalStats.projected.cgpa}</span>
                                <span className="text-slate-500 font-bold text-sm">Target</span>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className="text-xs font-bold text-emerald-500">+{Math.max(0, globalStats.projected.cgpa - globalStats.current.cgpa).toFixed(2)} Improvement</span>
                                <div className="h-1 flex-1 bg-slate-800 rounded-full overflow-hidden">
                                    <div className="h-full bg-emerald-500 w-[70%]" style={{ width: `${globalStats.projected.cgpa * 10}%` }}></div>
                                </div>
                            </div>
                        </div>

                        <div className="grid grid-cols-2 gap-3 pb-2">
                            <div className="bg-slate-800/50 p-4 rounded-3xl border border-slate-700/50">
                                <p className="text-[10px] font-bold text-slate-500 uppercase mb-1">Current</p>
                                <p className="text-xl font-black">{globalStats.current.cgpa}</p>
                            </div>
                            <div className="bg-emerald-500/10 p-4 rounded-3xl border border-emerald-500/20">
                                <p className="text-[10px] font-bold text-emerald-500 uppercase mb-1">Estimate</p>
                                <p className="text-xl font-black text-emerald-400">{globalStats.projected.cgpa}</p>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Prediction Model Settings */}
            <Card className="rounded-[2.5rem] border-slate-100 shadow-xl shadow-slate-200/50 overflow-hidden">
                <CardHeader className="bg-slate-50/50 border-b border-slate-100 p-6 flex flex-row items-center justify-between">
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-white rounded-2xl shadow-sm border border-slate-200 flex items-center justify-center text-emerald-600">
                            <ShieldCheck className="w-5 h-5" />
                        </div>
                        <div>
                            <CardTitle className="text-lg font-black text-slate-800">Prediction Model</CardTitle>
                            <CardDescription>Locally weighted regression based on {globalStats.current.courseCount} past data points.</CardDescription>
                        </div>
                    </div>
                    <div className="flex items-center gap-2">
                        <Button
                            variant={showPredictions ? "default" : "outline"}
                            size="sm"
                            onClick={() => setShowPredictions(!showPredictions)}
                            className={`rounded-full px-6 font-bold transition-all ${showPredictions ? 'bg-emerald-600 hover:bg-emerald-700' : ''}`}
                        >
                            {showPredictions ? 'Predictions Active' : 'Predictions Disabled'}
                        </Button>
                    </div>
                </CardHeader>
                <CardContent className="p-8 space-y-8">
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                        <div className="md:col-span-2 space-y-6">
                            <div className="flex justify-between items-center mb-2">
                                <Label className="text-sm font-black text-slate-700 uppercase tracking-wider">Confidence Modulation</Label>
                                <span className={`px-4 py-1 rounded-full text-[10px] font-black uppercase ${confidence < 1.0 ? 'bg-amber-100 text-amber-700' :
                                    confidence > 1.0 ? 'bg-blue-100 text-blue-700' :
                                        'bg-emerald-100 text-emerald-700'
                                    }`}>
                                    {confidence < 1.0 ? 'Conservative' : confidence > 1.0 ? 'Optimistic' : 'Realistic'} Mode
                                </span>
                            </div>
                            <input
                                type="range"
                                min="0.8"
                                max="1.2"
                                step="0.1"
                                value={confidence}
                                onChange={(e) => setConfidence(parseFloat(e.target.value))}
                                className="w-full h-2 bg-slate-100 rounded-lg appearance-none cursor-pointer accent-emerald-600"
                            />
                            <div className="flex justify-between text-[10px] font-bold text-slate-400 uppercase tracking-tighter">
                                <span>-20% Scale</span>
                                <span>Baseline (ML Estimate)</span>
                                <span>+20% Scale</span>
                            </div>
                        </div>

                        <div className="bg-slate-50 p-6 rounded-3xl border border-slate-100 space-y-4">
                            <h4 className="text-xs font-black text-slate-400 uppercase flex items-center gap-2">
                                <Zap className="w-3 h-3 text-emerald-500" /> Statistical Weighting
                            </h4>
                            <div className="space-y-3">
                                <div className="flex justify-between items-center">
                                    <span className="text-[10px] font-bold text-slate-600">Skill Cluster</span>
                                    <span className="text-[10px] font-black text-emerald-600">50% Impact</span>
                                </div>
                                <div className="flex justify-between items-center">
                                    <span className="text-[10px] font-bold text-slate-600">Level/Stage</span>
                                    <span className="text-[10px] font-black text-emerald-600">30% Impact</span>
                                </div>
                                <div className="flex justify-between items-center">
                                    <span className="text-[10px] font-bold text-slate-600">Performance Trend</span>
                                    <span className="text-[10px] font-black text-emerald-600">20% Impact</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>

            {/* Curriculum Table */}
            <Card className="rounded-[2.5rem] border-none shadow-2xl shadow-slate-200/50 overflow-hidden bg-white">
                <CardHeader className="p-8 pb-4">
                    <CardTitle className="text-2xl font-black text-slate-900">{level} Pathway</CardTitle>
                    <p className="text-sm font-medium text-slate-500">Track current scores and analyze potential performance.</p>
                </CardHeader>
                <CardContent className="p-0">
                    <div className="overflow-x-auto">
                        <Table>
                            <TableHeader className="bg-slate-50/50">
                                <TableRow className="border-slate-100 hover:bg-transparent">
                                    <TableHead className="w-[400px] pl-8 text-xs font-black text-slate-400 uppercase tracking-widest">Course Curriculum</TableHead>
                                    <TableHead className="text-center text-xs font-black text-slate-400 uppercase tracking-widest">Type</TableHead>
                                    <TableHead className="text-right pr-8 text-xs font-black text-slate-400 uppercase tracking-widest">Outcome / Prediction</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                <AnimatePresence mode="popLayout">
                                    {currentViewCourses.map((course) => {
                                        const pred = predictions[course.name];
                                        const override = overrides[domain][course.name];
                                        const isProjected = !course.grade && showPredictions && pred;

                                        return (
                                            <motion.tr
                                                key={course.id}
                                                initial={{ opacity: 0, x: -10 }}
                                                animate={{ opacity: 1, x: 0 }}
                                                className="group border-slate-50 hover:bg-slate-50/40 transition-all cursor-default"
                                            >
                                                <TableCell className="pl-8 py-6">
                                                    <div className="space-y-1">
                                                        <div className="flex items-center gap-3">
                                                            {course.grade ? (
                                                                <div className="w-5 h-5 rounded-full bg-emerald-100 flex items-center justify-center">
                                                                    <CheckCircle2 className="w-3 h-3 text-emerald-600" />
                                                                </div>
                                                            ) : (
                                                                <div className="w-5 h-5 rounded-full bg-slate-100 flex items-center justify-center">
                                                                    <Circle className="w-3 h-3 text-slate-300" />
                                                                </div>
                                                            )}
                                                            <span className={`font-black text-base tracking-tight ${course.grade ? 'text-slate-900' : 'text-slate-400'}`}>
                                                                {course.name}
                                                            </span>
                                                        </div>
                                                        <div className="flex items-center gap-2 pl-8">
                                                            <span className="text-[10px] font-bold text-slate-400 uppercase">{course.cluster}</span>
                                                            <span className="w-1 h-1 bg-slate-200 rounded-full"></span>
                                                            <span className="text-[10px] font-bold text-slate-400 uppercase">{course.credits} Credits</span>
                                                        </div>
                                                    </div>
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    {course.grade ? (
                                                        <span className="px-3 py-1 bg-emerald-100 text-emerald-700 rounded-full text-[10px] font-black uppercase tracking-wider">Completed</span>
                                                    ) : isProjected ? (
                                                        <span className="px-3 py-1 bg-blue-100 text-blue-700 rounded-full text-[10px] font-black uppercase tracking-wider">Predicted</span>
                                                    ) : (
                                                        <span className="px-3 py-1 bg-slate-100 text-slate-500 rounded-full text-[10px] font-black uppercase tracking-wider">Planned</span>
                                                    )}
                                                </TableCell>
                                                <TableCell className="text-right pr-8">
                                                    <div className="flex items-center justify-end gap-4">
                                                        {isProjected && (
                                                            <div className="text-right animate-in fade-in slide-in-from-right-2 duration-1000">
                                                                <div className="group/explain relative inline-block">
                                                                    <div className="flex items-center gap-1.5 cursor-help">
                                                                        <p className="text-[10px] font-black text-slate-400 uppercase tracking-tighter">Est. Value</p>
                                                                        <Info className="w-3 h-3 text-slate-300" />
                                                                    </div>
                                                                    <div className="absolute right-0 bottom-full mb-2 w-64 p-4 bg-slate-900 text-white rounded-[1.5rem] shadow-2xl opacity-0 scale-95 pointer-events-none group-hover/explain:opacity-100 group-hover/explain:scale-100 group-hover/explain:pointer-events-auto transition-all z-50">
                                                                        <h5 className="text-[10px] font-black text-emerald-400 uppercase tracking-widest mb-3">Model Influencers</h5>
                                                                        <div className="space-y-2">
                                                                            {pred.influencers.length > 0 ? pred.influencers.map((inf, k) => (
                                                                                <div key={k} className="flex justify-between items-center text-[10px]">
                                                                                    <span className="text-slate-400 truncate w-36 text-left">{inf.name}</span>
                                                                                    <span className="font-black text-emerald-400">{inf.weight}% Weight</span>
                                                                                </div>
                                                                            )) : (
                                                                                <p className="text-[10px] text-slate-500 italic">No related subjects found. Base average used.</p>
                                                                            )}
                                                                        </div>
                                                                        <div className="mt-4 pt-4 border-t border-slate-800">
                                                                            <p className="text-[9px] text-slate-500 leading-tight">Formula: Weighted Cluster Average + Trend Correction â€¢ Scaled by {confidence}x confidence.</p>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <p className="text-xl font-black text-blue-600 line-clamp-1">{pred.predictedGP}</p>
                                                            </div>
                                                        )}

                                                        <Select
                                                            value={course.grade ?? (override ?? "not_taken")}
                                                            onValueChange={(v) => {
                                                                if (course.grade) updateGrade(course.name, v === "not_taken" ? null : v as Grade);
                                                                else updateOverride(course.name, v === "not_taken" ? null : v as Grade);
                                                            }}
                                                        >
                                                            <SelectTrigger className={`w-36 h-11 border-none bg-slate-50 rounded-2xl focus:ring-0 font-black text-xs ${course.grade ? 'bg-emerald-50 text-emerald-700' :
                                                                override ? 'bg-orange-50 text-orange-600 border border-orange-100' :
                                                                    isProjected ? 'bg-blue-50/50 text-blue-600' : 'text-slate-400'
                                                                }`}>
                                                                <SelectValue placeholder="Not Taken" />
                                                            </SelectTrigger>
                                                            <SelectContent className="rounded-[1.5rem] border-slate-100 shadow-2xl">
                                                                <SelectItem value="not_taken" className="text-slate-400 italic font-medium">Not Selected</SelectItem>
                                                                <SelectGroup>
                                                                    <div className="px-2 py-1.5 text-[10px] font-black text-slate-400 uppercase tracking-widest">Valid Grades</div>
                                                                    {['S', 'A', 'B', 'C', 'D', 'E'].map(g => (
                                                                        <SelectItem key={g} value={g} className="font-black">
                                                                            Grade {g} â€” {GRADE_POINTS[g as Exclude<Grade, null>]}
                                                                        </SelectItem>
                                                                    ))}
                                                                </SelectGroup>
                                                                <SelectGroup>
                                                                    <div className="px-2 py-1.5 text-[10px] font-black text-slate-400 uppercase tracking-widest mt-2">Statuses</div>
                                                                    {['U', 'I', 'W'].map(g => (
                                                                        <SelectItem key={g} value={g} className="font-medium text-slate-400 italic">
                                                                            {g === 'U' ? 'U (Fail)' : g === 'I' ? 'I (Incomplete)' : 'W (Withdrawn)'}
                                                                        </SelectItem>
                                                                    ))}
                                                                </SelectGroup>
                                                            </SelectContent>
                                                        </Select>
                                                    </div>
                                                </TableCell>
                                            </motion.tr>
                                        );
                                    })}
                                </AnimatePresence>
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>

            {/* Tech Compliance Footer */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center px-12 py-10 bg-slate-900 rounded-[3rem] text-white overflow-hidden relative shadow-2xl">
                <div className="absolute bottom-0 left-0 w-64 h-64 bg-emerald-500/10 rounded-full blur-[100px] -ml-32 -mb-32"></div>
                <div className="space-y-4 relative z-10">
                    <div className="flex items-center gap-2">
                        <div className="w-8 h-8 bg-emerald-500 rounded-xl flex items-center justify-center font-black text-xs">AI</div>
                        <h4 className="text-lg font-black tracking-tight">Tech Stack Compliance</h4>
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest text-slate-500">
                            <CheckCircle2 className="w-3 h-3 text-emerald-500" /> 100% Client-Side
                        </div>
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest text-slate-500">
                            <CheckCircle2 className="w-3 h-3 text-emerald-500" /> Fully Deterministic
                        </div>
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest text-slate-500">
                            <CheckCircle2 className="w-3 h-3 text-emerald-500" /> Privacy Encrypted
                        </div>
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest text-slate-500">
                            <CheckCircle2 className="w-3 h-3 text-emerald-500" /> Curriculum Validated
                        </div>
                    </div>
                </div>
                <div className="text-right relative z-10">
                    <p className="text-[10px] font-medium text-slate-500 leading-relaxed italic">
                        &quot;Prediction is the beginning of planning. This model estimates your path based on similar cognitive patterns observed in past coursework.&quot;
                    </p>
                    <div className="mt-4 flex justify-end items-center gap-4 text-slate-600">
                        <Zap className="w-4 h-4" />
                        <TrendingUp className="w-4 h-4" />
                        <Scale className="w-4 h-4" />
                    </div>
                </div>
            </div>
        </div>
    );
}

